import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Product from "../components/Product/Product";
import { selectWishItems } from "../redux/selectors/wishlist";
import NoPhoto from "./../icons/30.png";

// First we take redux store data
const mapState = (state) => ({
	products: selectWishItems(state),
});

function Wishlist() {
	const { t } = useTranslation(["wishlist"]);
	const { products } = useSelector(mapState);
	useEffect(() => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
	}, []);
	return (
		<>
			{/* Big Header Start */}
			<section
				className=" text-center py-5 text-white mx-auto mt-3"
				style={{
					backgroundColor: "#6270FB",
					borderRadius: "20px",
					width: "90%",
				}}
			>
				<h2 style={{ fontWeight: 700 }}>{t("wishlist:Wishlist")}</h2>
			</section>
			{/* Big Header Start */}

			{/* Main Content Start */}
			<div className="container">
				{/* Breadcumb Start */}
				<div className="position-relative ">
					<div
						className="btn bg-white text-dark position-absolute"
						style={{
							top: "-15px",
							borderRadius: "20px",
							boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
						}}
					>
						<Link to="/" className="text-decoration-none">
							<i className="bi bi-house-door text-muted"></i>
						</Link>{" "}
						\ {t("wishlist:Wishlist")}
					</div>
				</div>
				{/* Main Start */}
				{products.length >= 1 ? (
					<div className="row py-5 row-cols-2 row-cols-sm-3 row-cols-md-5">
						{Array.isArray(products) &&
							products.map((product) => {
								return (
									<Product key={product?.id} cls="mt-md-4" item={product} />
								);
							})}
					</div>
				) : (
					<div className="row text-center py-5">
						<div className="">
							<img className="img-fluid" src={NoPhoto} alt="No-Product" />
						</div>
						<Link
							style={{ maxWidth: "300px", borderRadius: "15px" }}
							className="btn btn-primary mx-auto text-decoration-none "
							to="/"
						>
							{t("wishlist:goShop")}
						</Link>
					</div>
				)}
				{/* Breadcumb End */}
			</div>
		</>
	);
}

export default Wishlist;
